.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
  z-index: 1;

  &--active {
    opacity: 1;
    pointer-events: all;
  }

  &__content {
    padding: 20px;
    width: 50vw;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.3s all;

    &--active {
      transform: scale(1);
    }
  }
}
